*{margin:0;padding:0;box-sizing:border-box}body{background:#fff;color:#222;font-family:'Noto Sans JP', sans-serif}


*{margin:0;padding:0;box-sizing:border-box}body{background:#fff;color:#222;font-family:'Noto Sans JP', sans-serif}.navbar{background:#222;padding:0.5rem 1rem;display:grid;grid-template-columns:auto 1fr}.nav-links{align-self:center;list-style:none;display:grid;grid-template-columns:repeat(3, 1fr);text-align:center}.nav-link{color:#ececec;text-transform:uppercase;text-decoration:none;transition:all 0.3s linear}.nav-link:hover{color:#82b340}.active{color:#82b340}

*{margin:0;padding:0;box-sizing:border-box}body{background:#fff;color:#222;font-family:'Noto Sans JP', sans-serif}.tour-list{width:70vw;margin:0 auto;padding:2rem}@media screen and (min-width: 768px){.tour-list{display:grid;grid-template-columns:repeat(2, 1fr);grid-column-gap:1.5rem}}@media screen and (min-width: 992px){.tour-list{width:85vw;grid-template-columns:repeat(3, 1fr)}}@media screen and (min-width: 1140px){.tour-list{grid-template-columns:repeat(4, 1fr)}}

*{margin:0;padding:0;box-sizing:border-box}body{background:#fff;color:#222;font-family:'Noto Sans JP', sans-serif}.tour{background:#ececec;border-radius:0.8rem;overflow:hidden;margin:1rem 0}.tour img{width:100%;display:block}.img-container{position:relative}.img-container:hover .close-btn{opacity:1}.close-btn{position:absolute;display:block;top:5%;right:5%;color:#82b340;font-size:2rem;cursor:pointer;transition:all 0.3s linear;opacity:0}.tour-info{padding:1rem 0.5rem}.tour-info h3{text-transform:uppercase;color:#82b340}.tour-info h4{text-transform:capitalize;color:#222;margin:0.2rem 0 0.6rem 0}.tour-info h5{text-transform:capitalize}.tour-info h5 span{color:#82b340;margin:0.5rem;font-size:1.2rem;cursor:pointer}

